.listitem-img{
    padding: 5px;
    width: 110px;
    height: 110px;
    float: left;
}

.listitem-div{
    padding: 5px;
    padding-top: 25px;
    width: 236px;
    height: 110px;
    float: left;
}
.listitem-span-three{
    padding-top: 10px;
    width: 236px;
}
.listitem-span-one{
    margin-top: 11.5px;
    height: 37px;
    position: relative;
    overflow: hidden;
    line-height: 18.5px;
    text-overflow: ellipsis;
    min-height: 0;
    font-size: 20px;
    color: #333333;
    font-family: "Microsoft YaHei";
}

.listitem-span-four{
    font-size: 11px;
    color: red;
}
.listitem-span-two{
    font-size: 14px;
    line-height: 16px;
    color: #ff5500;
}


.listitem-btn{
    width: 80px;
    height: 40px;
    border: none;
    background-color: #FF0036;
    line-height: 34px;
    margin-top: 0px;
    color: white;
    border-bottom-right-radius: 15px;
    border-top-right-radius: 15px;
    border-top-left-radius: 15px;
    border-bottom-left-radius: 15px;

}